.travel-gallery {
	background: url('../img/bg/travel-gallery_bg.jpg') repeat-y top center;
	&__container {
		font-size: 0; }
	&__row {
		margin: 0 -8px; }
	&__item {
		display: inline-block;
		vertical-align: top;
		width: calc(25% - 16px);
		margin: 0 8px 7px;
		&:hover &-text {
			color: #22273b; }
		&-pic-wrap {
			border: 6px solid #fff; }
		&-pic {
			padding-top: 77%;
			@include cover; }
		&-text {
			padding: 10px;
			text-align: center;
			@include f('cb', 18);
			line-height: 1.2;
			color: $white;
			@include trs; } }
	&__actions {
		padding: 53px 0 0;
		text-align: center;
		&-btn {
			margin: 0 13px;
			min-width: 270px;
			font-size: 18px;
			&:hover {
				border-color: #22273b;
				color: #22273b; } } }
	&_default {
		padding: 95px 0 40px; } }
