.view {
	&__container {
		@include xs {
			max-width: 620px; }
		@include r(479) {
			max-width: 330px; } }
	&__item {
		position: relative;
		padding-top: 100%;
		cursor: pointer;
		&-img {
			@include coverdiv;
			@include cover; }
		&-zoom {
			@include coverdiv;
			z-index: 2;
			transform: scale(.5);
			transition: transform 0.4s cubic-bezier(0.55, 0.06, 0.68, 0.19), background 0.6s ease-in-out;
			@include vertical;
			@include xs {
				transform: none;
				background-color: transparent; } }
		&-zoom-icon {
			display: inline-block;
			vertical-align: middle;
			font-size: 52px;
			opacity: 0;
			transform: scale(.7);
			transition: transform 0.5s cubic-bezier(0.6, -0.28, 0.74, 0.05), opacity .5s ease-in-out;
			@include xs {
				position: absolute;
				bottom: 15px;
				right: 15px;
				font-size: 32px;
				opacity: 1;
				transform: none; } }
		&:hover &-zoom {
			transform: scale(1);
			background: rgba(#1095ff, .7);
			@include xs {
				transform: none;
				background: transparent; } }
		&:hover &-zoom-icon {
			opacity: 1;
			transform: scale(1); } }
	&__slider {
		&-arr {
			position: absolute;
			top: calc(50% - 21px);
			background: none;
			z-index: 3;
			@include md {
				top: calc(50% - 18px); }
			&_prev {
				left: 14px;
				@include xs {
					left: 5px; } }
			&_next {
				right: 14px;
				@include xs {
					right: 5px; } } }
		&-icon {
			fill: $white;
			font-size: 42px;
			@include md {
				font-size: 36px; } }
		.owl-prev.disabled .view__slider-icon,
		.owl-next.disabled .view__slider-icon {
			fill: rgba($white, .7); } }

	&_excursion &__container {
		padding: 0; } }
